/* -------------------------------------------------------------------------- 
	#. Alerts
  -------------------------------------------------------------------------- */
.adfy__woofc-alert {
  display: block;
  color: #444444;
  padding: 10px 15px;
  margin-bottom: 20px;
  border-radius: 4px;
  overflow: hidden;
  background-color: #EEEEEE;
}
.adfy__woofc-alert .adfy__woofc-alert-text {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: 8px;
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.4;
}
.adfy__woofc-alert .adfy__woofc-alert-text svg {
  color: #444444;
  width: 14px;
  height: 14px;
}

.adfy__woofc-alert.success {
  color: #67C23A;
  background-color: #F0F9EB;
}
.adfy__woofc-alert.success svg {
  color: #67C23A;
}

.adfy__woofc-alert.error {
  color: #F56C6C;
  background-color: #ffe6e6;
}
.adfy__woofc-alert.error svg {
  color: #F56C6C;
}

/* -------------------------------------------------------------------------- 
	#. General button
  -------------------------------------------------------------------------- */
.adfy__woofc-button,
a.adfy__woofc-button,
button.adfy__woofc-button,
input.adfy__woofc-button[type=submit],
button.adfy__woofc-button[type=submit] {
  display: inline-flex;
  gap: 5px;
  align-items: center;
  font-size: var(--adfy_woofc_base_button_font_size);
  letter-spacing: var(--adfy_woofc_base_button_letter_spacing);
  font-weight: var(--adfy_woofc_base_button_font_weight);
  line-height: 1;
  fill: var(--adfy_woofc_primary_button_label_color);
  color: var(--adfy_woofc_primary_button_label_color);
  border-width: 1px;
  border-style: solid;
  border-color: var(--adfy_woofc_primary_button_border_color);
  padding: 15px 25px;
  border-radius: var(--adfy_woofc_base_button_border_radius);
  box-shadow: none;
  text-decoration: none;
  background: transparent;
  appearance: button;
  text-transform: var(--adfy_woofc_base_button_text_transform);
  background-color: var(--adfy_woofc_primary_button_background_color);
  transition: all 0.5s ease;
}
.adfy__woofc-button:hover,
a.adfy__woofc-button:hover,
button.adfy__woofc-button:hover,
input.adfy__woofc-button[type=submit]:hover,
button.adfy__woofc-button[type=submit]:hover {
  fill: var(--adfy_woofc_primary_button_label_color_hover);
  color: var(--adfy_woofc_primary_button_label_color_hover);
  border-color: var(--adfy_woofc_primary_button_border_color_hover);
  box-shadow: none;
  text-decoration: none;
  background: transparent;
  background-color: var(--adfy_woofc_primary_button_background_color_hover);
}
.adfy__woofc-button:focus,
a.adfy__woofc-button:focus,
button.adfy__woofc-button:focus,
input.adfy__woofc-button[type=submit]:focus,
button.adfy__woofc-button[type=submit]:focus {
  outline: 0;
  box-shadow: none;
  text-decoration: none;
}

.adfy__woofc-button.close,
.adfy__woofc-button.secondary,
a.adfy__woofc-button.secondary,
.adfy__woofc-button.continue-shopping {
  fill: var(--adfy_woofc_secondary_button_label_color);
  color: var(--adfy_woofc_secondary_button_label_color);
  border-color: var(--adfy_woofc_secondary_button_border_color);
  background-color: var(--adfy_woofc_secondary_button_background_color);
}
.adfy__woofc-button.close:hover,
.adfy__woofc-button.secondary:hover,
a.adfy__woofc-button.secondary:hover,
.adfy__woofc-button.continue-shopping:hover {
  fill: var(--adfy_woofc_secondary_button_label_color_hover);
  color: var(--adfy_woofc_secondary_button_label_color_hover);
  border-color: var(--adfy_woofc_secondary_button_border_color_hover);
  background-color: var(--adfy_woofc_secondary_button_background_color_hover);
}

/* -------------------------------------------------------------------------- 
	#. Fake button
  -------------------------------------------------------------------------- */
.adfy__woofc-fake-button {
  display: inline-flex;
  fill: currentColor;
  color: currentColor;
  cursor: pointer;
  width: auto;
  height: auto;
  padding: 0;
  margin: 0;
  font-size: 15px;
  font-weight: normal;
  line-height: 1;
  border: none;
  background: transparent;
  background-color: transparent;
  text-decoration: none;
  box-shadow: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: all 0.5s ease;
}
.adfy__woofc-fake-button:hover {
  fill: currentColor;
  color: currentColor;
  background: transparent;
  background-color: transparent;
  text-decoration: none;
  box-shadow: none;
  border: none;
}
.adfy__woofc-fake-button:focus {
  outline: 0;
  text-decoration: none;
  box-shadow: none;
  border: none;
}

/* -------------------------------------------------------------------------- 
	#. Notfy button
  -------------------------------------------------------------------------- */
.notyf {
  color: var(--adfy_woofc_toast_text_color);
}
.notyf .notyf__toast[class*=adfy__woofc-notfy] {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
}
.notyf .notyf__toast[class*=adfy__woofc-notfy] .notyf__wrapper .notyf__icon .notyf__icon--error,
.notyf .notyf__toast[class*=adfy__woofc-notfy] .notyf__wrapper .notyf__icon .notyf__icon--success {
  background: var(--adfy_woofc_toast_text_color);
}
.notyf .notyf__toast[class*=adfy__woofc-notfy] .notyf__wrapper .notyf__message {
  font-size: 15px;
  font-weight: normal;
  line-height: 1.4;
  color: var(--adfy_woofc_toast_text_color);
}
.notyf .notyf__toast[class*=adfy__woofc-notfy] .notyf__wrapper .notyf__message .adfy__woofc-notfy-button {
  display: inline-flex;
  align-items: center;
  font-size: 13px;
  text-transform: inherit;
  letter-spacing: inherit;
  padding: 5px 10px 6px 10px;
  border-radius: 3px;
  color: var(--adfy_woofc_toast_button_text_color);
  background-color: var(--adfy_woofc_toast_button_background_color);
}
.notyf .notyf__toast[class*=adfy__woofc-notfy] .notyf__wrapper .notyf__message .adfy__woofc-notfy-button:hover {
  color: var(--adfy_woofc_toast_button_text_color_hover);
  background-color: var(--adfy_woofc_toast_button_background_color_hover);
}
.notyf .notyf__toast .notyf__ripple {
  background: var(--adfy_woofc_toast_background_color) !important;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart Button
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger {
  position: fixed;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: var(--adfy_woofc_toggle_button_size);
  height: var(--adfy_woofc_toggle_button_size);
  left: auto;
  right: var(--adfy_woofc_toggle_button_horizental_offset);
  bottom: var(--adfy_woofc_toggle_button_vertical_offset);
  border-width: 1px;
  border-style: solid;
  border-color: var(--adfy_woofc_toggle_button_border_color);
  z-index: var(--adfy_woofc_trigger_button_zindex);
  fill: var(--adfy_woofc_toggle_button_text_color);
  color: var(--adfy_woofc_toggle_button_text_color);
  cursor: pointer;
  background-color: var(--adfy_woofc_toggle_button_background_color);
  border-radius: var(--adfy_woofc_toggle_button_border_radius);
  line-height: 1;
  font-weight: normal;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
}
#adfy__woofc-trigger .icon svg {
  height: var(--adfy_woofc_toggle_button_cart_icon_font_size);
  width: var(--adfy_woofc_toggle_button_cart_icon_font_size);
}
#adfy__woofc-trigger .badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -5px;
  right: auto;
  bottom: auto;
  left: -10px;
  height: 23px;
  width: 23px;
  font-size: 13px;
  font-weight: normal;
  line-height: 1;
  color: var(--adfy_woofc_toggle_button_badge_text_color);
  border-radius: 3px;
  background-color: var(--adfy_woofc_toggle_button_badge_background_color);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:hover {
  fill: var(--adfy_woofc_toggle_button_text_color_hover);
  color: var(--adfy_woofc_toggle_button_text_color_hover);
  border-color: var(--adfy_woofc_toggle_button_border_color_hover);
  background-color: var(--adfy_woofc_toggle_button_background_color_hover);
  box-shadow: 0px 0px 60px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:hover .badge {
  color: var(--adfy_woofc_toggle_button_badge_text_color_hover);
  background-color: var(--adfy_woofc_toggle_button_badge_background_color_hover);
}
#adfy__woofc-trigger:focus {
  outline: none;
}
#adfy__woofc-trigger.top-right {
  bottom: auto;
  left: auto;
  right: var(--adfy_woofc_toggle_button_horizental_offset);
  top: var(--adfy_woofc_toggle_button_vertical_offset);
}
#adfy__woofc-trigger.top-left {
  bottom: auto;
  right: auto;
  top: var(--adfy_woofc_toggle_button_vertical_offset);
  left: var(--adfy_woofc_toggle_button_horizental_offset);
}
#adfy__woofc-trigger.bottom-left {
  top: auto;
  right: auto;
  bottom: var(--adfy_woofc_toggle_button_vertical_offset);
  left: var(--adfy_woofc_toggle_button_horizental_offset);
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart
  -------------------------------------------------------------------------- */
#adfy__floating-cart * {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

#adfy__floating-cart {
  display: block;
  position: fixed;
  color: var(--adfy_woofc_base_text_color);
  font-size: var(--adfy_woofc_base_text_font_size);
  background-color: var(--adfy_woofc_cart_background_color);
  box-sizing: border-box;
  transition: all 0.6s ease-in-out;
}
#adfy__floating-cart .adfy_woofc-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  height: 100%;
  width: 100%;
  position: relative;
  padding: 30px;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner {
    padding: 20px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header {
  display: grid;
  grid-template-columns: auto 30px;
  column-gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--adfy_woofc_border_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__woofc-title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  font-size: 20px;
  margin: 0;
  padding: 0;
  color: var(--adfy_woofc_cart_title_text_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__woofc-title .adfy__woofc-badge {
  align-items: center;
  color: var(--adfy_woofc_cart_title_count_badge_text_color);
  padding: 7px 10px 8px 10px;
  font-size: calc(var(--adfy_woofc_base_text_font_size) - 20%);
  line-height: 1;
  font-weight: normal;
  border-radius: 15px;
  background-color: var(--adfy_woofc_cart_title_count_badge_background_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc {
  display: inline-flex;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  line-height: 1;
  font-weight: normal;
  fill: var(--adfy_woofc_cart_close_button_text_color);
  color: var(--adfy_woofc_cart_close_button_text_color);
  border: none;
  border-radius: 100%;
  background-color: transparent;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc svg {
  width: 22px;
  height: 22px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc:hover {
  fill: var(--adfy_woofc_cart_close_button_text_color_hover);
  color: var(--adfy_woofc_cart_close_button_text_color_hover);
  border: none;
  background-color: transparent;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 20px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar.adfy__woofc-hidden {
  display: none;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .adfy__woofc-shipping-text {
  font-size: var(--adfy_woofc_base_text_font_size);
  padding: 0;
  margin: 0 0 10px 0;
  font-weight: normal;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .adfy__woofc-shipping-text .amount {
  font-weight: bold;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars {
  position: relative;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars .shipping-bar {
  position: relative;
  display: block;
  height: 8px;
  width: 100%;
  border-radius: 10px;
  background-color: var(--adfy_woofc_shopping_meter_initial_background_color);
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars .shipping-bar {
    height: 5px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars .progress-bar {
  position: absolute;
  left: 0;
  top: 0;
  right: auto;
  z-index: 2;
  background-color: var(--adfy_woofc_shopping_meter_progress_background_color);
  transition: all 1s cubic-bezier(0.4, 0, 1, 1);
}
#adfy__floating-cart .adfy_woofc-inner #adfy__woofc-cart-errors {
  display: flex;
