  border-color: var(--adfy_woofc_border_color);
  background: var(--adfy_woofc_cart_input_background_color);
  box-shadow: none;
  box-sizing: border-box;
  border-radius: 3px;
  -webkit-box-shadow: inset 1px 1px 3px transparent;
  -moz-box-shadow: inset 1px 1px 3px transparent;
  box-shadow: inset 1px 1px 3px transparent;
  transition: all 0.5s ease;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs input[type=text]:focus {
  outline: none;
  border-color: #3DA6FF;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs .adfy__woofc-button {
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons {
  position: relative;
  padding: 20px;
  border-radius: 5px;
  border: 2px dashed var(--adfy_woofc_border_color);
  margin-top: 40px;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons.hidden {
  display: none;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons .title {
  position: absolute;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  top: -15px;
  right: auto;
  left: 15px;
  font-size: 15px;
  font-size: calc(var(--adfy_woofc_base_text_font_size) - 5%);
  line-height: 1.4;
  font-weight: normal;
  padding: 5px 10px;
  background-color: var(--adfy_woofc_cart_background_color);
  z-index: 1;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons .title svg {
  width: 18px;
  height: 18px;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 15px;
  list-style: none;
  margin: 10px 0 0 0;
  padding: 0;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons ul li {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: auto 50px;
  column-gap: 20px;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 10px 15px;
  border-radius: 4px;
  border: 1px solid var(--adfy_woofc_border_color);
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons ul li input[type=text] {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  max-width: 100%;
  font-size: 15px;
  font-weight: normal;
  line-height: 1.4;
  color: var(--adfy_woofc_base_text_color);
  padding: 0;
  height: auto;
  min-height: 1px;
  border-width: 0;
  border-style: solid;
  border-color: transparent;
  background: var(--adfy_woofc_cart_background_color);
  box-shadow: none;
  box-sizing: border-box;
  border-radius: 0;
  box-shadow: none;
  cursor: default;
  transition: none;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons ul li input[type=text]:focus {
  outline: none;
  box-shadow: none;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons ul li input[type=text]:hover {
  box-shadow: none;
  text-decoration: none;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons .adfy__woofc-remove-applied-coupon-button {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 50px;
  height: 30px;
  border-radius: 3px;
}
#adfy__woofc-coupon-container #adfy__woofc-applied-coupons .adfy__woofc-remove-applied-coupon-button svg {
  height: 18px;
  width: 18px;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart Mask
  -------------------------------------------------------------------------- */
#adfy__woofc-overlay {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: var(--adfy_woofc_cart_overlay_background_color);
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s linear;
}

.adfy__woofc-visible #adfy__woofc-overlay {
  opacity: 1;
  visibility: visible;
  z-index: var(--adfy_woofc_overlaymask_zindex);
}

/* -------------------------------------------------------------------------- 
	#. Utility Class
  -------------------------------------------------------------------------- */
.adfy__hide-woofc {
  cursor: pointer;
}

.adfy__woofc-visible {
  overflow: hidden;
}

.adfy__woofc-hidden {
  display: none;
}

.adfy__woofc-link.has-underline {
  position: relative;
  border: none;
  text-decoration: none;
  color: var(--adfy_woofc_base_link_color);
  background: transparent;
  background-color: transparent;
  transition: all 0.5s ease;
}
.adfy__woofc-link.has-underline::after, .adfy__woofc-link.has-underline::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: currentColor;
}
.adfy__woofc-link.has-underline::before {
  content: "";
  z-index: 1;
  background-color: currentColor;
  transform-origin: right center;
  transform: scaleX(0);
  will-change: scale;
  transition: transform 0.26s cubic-bezier(0.25, 0.8, 0.25, 1);
  -webkit-backface-visibility: hidden;
}
.adfy__woofc-link.has-underline::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  opacity: 0.6;
  background-color: currentColor;
}
.adfy__woofc-link.has-underline:hover {
  color: var(--adfy_woofc_base_link_color_hover);
  text-decoration: none;
}
.adfy__woofc-link.has-underline:hover::before {
  content: "";
  transform: scale(1);
  transform-origin: left center;
  background-color: var(--adfy_woofc_base_link_color_hover);
}
.adfy__woofc-link.has-underline:focus {
  outline: none;
  text-decoration: none;
}

/* -------------------------------------------------------------------------- 
	#. Fade in
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartFadeIn {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0.4;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
	#. Fade Down
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartFadeDown {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/* -------------------------------------------------------------------------- 
	#. Slide to left
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartSlideToLeft {
  from {
    right: -100vw;
    opacity: 0;
  }
  to {
    right: 0;
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
	#. Slide to right
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartSlideToRight {
  from {
    right: 0;
    opacity: 1;
  }
  to {
    right: -100vw;
    opacity: 0;
  }
}